.lb-box-background .lb-box-content {
  width: 100px;
  height: 120px;
  box-sizing: content-box;
  margin: 0;
  background: #fff;
  position: absolute;
  top: 50%;
  left: 50%;
  padding: 1rem;
  margin-right: -50%;
  transform: translate(-50%, -50%);
  border: #777 solid 1px;
  box-shadow: 0 0 5px #333;
  cursor: default;
  -webkit-transition: all 0.6s ease-out;
  transition: all 0.6s ease-out;
